import React, { Component } from 'react';
import { ArrowDown } from '.';

export default class ArrowUp extends Component {
	render() {
		return (
			<ArrowDown transform="translate(6.853500, 6.853500) scale(1, -1) translate(-6.853500, -6.853500)" />
		);
	}
}



